.overlay {
    background-color: black; /*   */
    opacity: 0; /*   */
    position: fixed; /*   */
    z-index: 9980;
    right: 0;    /*  0 */
    top: 0;    /*  0 */
    left: 0;    /*  0 */
    bottom: 0;    /*  0 */
    visibility: hidden; /*   */

    /* transition  */
    transition: 0.5s ease;
}

.overlay:target {
    visibility: visible; /*   */
    opacity: 0.8;    /*     */
}

.popup {
    position: fixed;    /*   */
    visibility: hidden;    /*  -  */
    z-index: 9999;    /* z-index */
    transform: translateX(-50%);
    left: 50%;
    top: 150px;
    width: 400px;
    text-align: center;
    box-sizing: border-box;
    padding: 40px 20px;
    background-color: #fff; /*  */
    color: $black; /*  */
    font-size: 20px;
    font-weight: 300;
    border: 5px solid $orange;
}

.overlay:target+.popup {
    opacity: 1; /*   */
    visibility: visible; /*   */
}

.popup-close {
  display: block;
  width: 3px;
  height: 20px;
  background: $orange-dark;
  position: absolute;
  top: 7px;
  right: 15px;
  font-size: 0px;
  transform: rotate(45deg);
}

.popup-close:after {
  content: "";
  display: block;
  transform: rotate(-90deg);
  width: 3px;
  height: 20px;
  background: $orange-dark;
  position: absolute;
  top: 0;
}
