.footer {
	background: #000000;
	color: #818080;
	padding-top: 60px;
	padding-bottom: 60px;
	font-size: 18px;
}

.footer__line {
	padding-top: 30px;
}

.footer__line:after {
	content: "";
	display: table;
	clear: both;
}

.footer__text {
	float: left;
	width: 550px;
	padding: 0;
	margin: 0;
}

.footer__link {
	display: block;
	float: right;
	max-width: 300px;
	text-align: right;
	text-decoration: none;
	color: #ffa000;
	transition: all 0.3s ease;
	padding-bottom: 3px;
	border-bottom: 1px solid #000000;
}

.footer__link:hover {
	color: #fca91e;
	border-bottom: 1px solid #fca91e;
}

.footer__link:active {
	color: #e79101;
	border-bottom: 1px solid #e79101;
}

.header {
	background: #000000;
	padding: 20px 0;
}

.header__contacts {
	float: right;
	margin-top: 22px;
	font-weight: 300;
	position: relative;
	text-align: right;
}

.header__contacts:before {
	content: "";
	background-repeat: no-repeat;
	display: block;
	position: absolute;
	left: 0;
	transform: translateY(-50%);
	top: 50%;
}

.header__contacts--phone {
	margin-left: 80px;
	padding-left: 38px;
	min-width: 100px;
}

.header__contacts--phone:before {
	width: 23px;
	height: 36px;
	background-image: url("../img/icon-phone.png");
}

.header__contacts--email {
	padding-left: 48px;
	min-width: 100px;
}

.header__contacts--email:before {
	width: 36px;
	height: 23px;
	background-image: url("../img/icon-mail.png");
}

.header__link {
	color: white;
	text-decoration: none;
	padding-bottom: 3px;
	border-bottom: 1px solid #000000;
	transition: all 0.3s ease;
}

.header__link:hover {
	color: #fca91e;
	border-bottom: 1px solid #fca91e;
}

.header__link:active {
	color: #e79101;
	border-bottom: 1px solid #e79101;
}

.logo {
	float: left;
	width: 222px;
	height: auto;
}

.advantage-block {
	background-image: url("../img/advantage-bg.jpg");
	background-color: #ececec;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	padding-top: 140px;
	padding-bottom: 180px;
}

.advantage-block__title {
	font-size: 30px;
	font-weight: 400;
	text-align: center;
	padding-bottom: 90px;
}

.advantages {
	font-size: 0;
	text-align: center;
}

.advantage {
	display: inline-block;
	vertical-align: top;
	font-size: 20px;
	width: 255px;
	margin-right: 50px;
}

.advantage:last-child {
	margin-right: 0;
}

.advantage__icon {
	width: 110px;
	height: 110px;
	border-radius: 50%;
	background: #fff;
	margin: 0 auto;
	margin-bottom: 40px;
	position: relative;
}

.advantage__icon:before {
	position: absolute;
	content: "";
	transform: translate(-50%, -50%);
	top: 50%;
	left: 50%;
}

.advantage__icon--cash-register:before {
	background: url("../img/icon-cash-register.png") no-repeat;
	width: 75px;
	height: 56px;
}

.advantage__icon--bus:before {
	background: url("../img/icon-bus.png") no-repeat;
	width: 83px;
	height: 49px;
}

.advantage__icon--clock:before {
	background: url("../img/icon-clock.png") no-repeat;
	width: 74px;
	height: 63px;
